export default {
  methods: {
    // 如果需要弹出登录确认框，说明就是没登录，没登录是干不了下单操作的
    // return true 就是弹出了登录确认框，return false 就是没弹出登录确认框
    loginConfirm() {
      // 如果没有token就提示登录
      if (!this.$store.getters.token) {

        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作哦',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // on confirm
            this.$router.replace({
              path: '/login',
              query: {
                fromUrl: this.$route.fullPath,
                toUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => {
            // on cancel
          })
        return true// 这里没有token要记得return
      } else return false
    }
  }
}
